<template>
  <div class="page-wrap padding">
    <el-pagination background :layout="layout" :total="pageInfo.totalElements" :current-page.sync="pageInfo.currentPage" :page-sizes.sync="pageSizes" :page-size="pageSize" @current-change="currentChange" @size-change="sizeChange">
    </el-pagination>
  </div>
</template>
<script>
export default {
  name: 'Pagination',
  props: {
    pageInfo: {
      type: Object,
      required: true,
      default: function () {
        return {}
      }
    },
    pageSizes: {
      type: Array,
      default () {
        return [10, 50, 100, 200, 500]
      }
    },
    limit: {
      type: Number,
      default: 10
    },
    layout: {
      type: String,
      default: 'total,sizes, prev, pager, next, jumper'
    }
  },
  computed: {
    pageSize: {
      get () {
        return this.limit
      },
      set (val) {
        this.$emit('update:limit', val)
      }
    }
  },
  methods: {
    sizeChange (e) {
      this.$emit('sizeChange', e)
    },
    currentChange (e) {
      this.$emit('currentChange', e)
    }
  }
}
</script>



